.workplace-menu {
    width: 100%;
    height: 80px;
    padding: 12px 20px;
    margin: 8px 0;
    border: 4px solid #ccc;
    border-radius: 20px;
    box-sizing: border-box;
    background-color: lightblue;
    display: inline-block;
}

.workplace-logout {
    position: absolute;
    top: 0px;
    right: 50px;
}

.workplace-logout:hover {
    position: absolute;
    top: 0px;
    right: 50px;
    cursor: pointer;
    color: wheat;
}


.workplace-message {
    position: absolute;
    top: 0px;
    left: 10px;
}

.dropbtn {
    background-color: lightblue;
    color: black;
    padding: 10px;
    font-size: 30px;
    border: none;
    cursor: pointer;
    font-weight: bold;

  }
  
  .dropdown {
    position: relative;
    display: inline-block;
  }
  
  .dropdown-content {
    display: none;
    position: absolute;
    background-color: #f9f9f9;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
    cursor: pointer;
  }
  
  .dropdown-content a {
    color: black;
    background-color: lightblue;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
    cursor: pointer;
  }
  
  .dropdown-content a:hover {
    background-color: #f1f1f1
  }
  
  .dropdown:hover .dropdown-content {
    display: block;
  }
  
  .dropdown:hover .dropbtn {
    background-color: lightblue;
    color: wheat;
  }

  .order-outer-div{
    width: 200px;
    padding: 12px 20px;
    margin: 8px 0;
    border: 5px solid grey;
    border-radius: 20px;
    box-sizing: border-box;
    position: absolute;
    left: 62.5px;
    top: 200px;
    background-color: wheat;
    cursor: pointer;
  }

  .orders {
    width: 500px;
    position: relative;
    left: 300px;
    top: 50px;
    /*background-color: wheat;
    margin: 8px 0;
    border: 4px solid #cccccc;
    border-radius: 20px;*/
  }

  .order {
    width: 500px;
    background-color: white;
    color: black;
    margin: 8px 0;
    border: 4px solid #ccc;
    border-radius: 20px;
    display: flex;
  }

  .next-page-order{
    position: relative;
    left: 370px;
  }

  .button-page-order{
    padding: 2px 2px;
    margin: 1px 0;
    border: 1px solid #ccc;
    border-radius: 10px;
    box-sizing: border-box;
    background-color: lightblue;
    display: inline-block;
    font-size: 20px;
    margin-left: 5px;
  }

  .button-page-order:hover{
    cursor: pointer;
    background-color: wheat;
    color: black;
  }

  .order-info{
    margin-left: 20px;
  }

  .view-more:hover{
    cursor: pointer;
  }

  .searchOrderInput{
    width: 55%;
    padding: 2px 2px;
    margin: 1px 0;
    border: 1px solid #ccc;
    border-radius: 10px;
    box-sizing: border-box;
    background-color: white;
    display: inline-block;
    font-size: 20px;
    margin-left: 5px;
  }

  .searchOrderInput:focus{
    outline: none;
  }

  .searchOrderButton{
    width: 20%;
    margin-left: 1px;
    padding: 2px 2px;
    margin: 1px 0;
    border: 1px solid #ccc;
    border-radius: 10px;
    box-sizing: border-box;
    background-color: white;
    display: inline-block;
    font-size: 20px;
    margin-left: 5px;
    background-color: lightblue;
  }

  .searchOrderButton:hover{
    cursor: pointer;
    background-color: wheat;
  }