.login-div-external {
    border-radius: 25px;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -250px;
    margin-left: -350px;
    width: 700px;
    height: 500px;
    background-color: lightblue;
}

.login-div-internal {
    border-radius: 25px;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -175px;
    margin-left: -250px;
    width: 500px;
    height: 350px;
    background-color: white;
}

.login-message {
    margin-left: 50px
}

.login-username {
    width: 375px;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 20px;
    box-sizing: border-box;
    position: absolute;
    left: 62.5px;
    top: 50px;
}

.login-username:focus {
    outline: none;
}

.login-password {
    width: 375px;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 20px;
    box-sizing: border-box;
    position: absolute;
    left: 62.5px;
    top: 125px;
}

.login-password:focus{
    outline: none;
}

.login-login-button {
    width: 150px;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 20px;
    box-sizing: border-box;
    position: absolute;
    left: 287.5px;
    top: 200px;
    background-color: lightblue;
    cursor: pointer;
}

.login-login-button:hover {
    background-color: blue;
    color: wheat;
}

.login-new-account {
    width: 200px;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 20px;
    box-sizing: border-box;
    position: absolute;
    left: 62.5px;
    top: 200px;
    background-color: lightblue;
    cursor: pointer;
}

.login-new-account:hover {
    background-color: blue;
    color: wheat;
}